body {
    font-family: 'MyFont'; /* use 'MyFont' and fall back to 'sans-serif' if 'MyFont' is not available */
}
.recipePage{
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    top:130px;
    font-size: 40px;
}
.grid-container {
    border: 1px solid #ddd; /* íƒœë‘ë¦¬ ìƒ‰ìƒ ì„¤ì • */
    padding: 20px; /* ë‚´ë¶€ íŒ¨ë”© ì„¤ì • */
