}
.recipe-grid {
    width: 1300px; /* ê·¸ë¦¬ë“œ ë„ˆë¹„ ì„¤ì • */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: auto;
    grid-auto-flow: row;
    gap: 20px;
    padding: 20px;
    box-sizing: border-box;
    z-index: 1 ;
}
.click-button{
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-right: 40px;
}
.button-d{
    margin: 5px;
    border: none;
    cursor: pointer;
    height: 30px;
    width: 80px;
    align-items: center;
    background-color: #ffcc6d;
    border-radius: 5px;
    color:white;
    font-size: 15px;
    font-weight: 800;
    justify-content:center;
}
.recipe-item {
    border: 1px solid #ddd;
    border-radius: 10px;
    padding: 10px;
    box-sizing: border-box;
    transition: all 0.3s ease;
    width: 300px; /* ì¹´ë“œì˜ ë„ˆë¹„ ì„¤ì • */
    height: 300px; /* ì¹´ë“œì˜ ë†’ì´ ì„¤ì • */
