    justify-content: center;
    align-items: center;
}

.recipe-img {
    margin: -10px 0 0 -10px;
    width: 300px;
    height: 200px;
    object-fit: cover;
    display: flex;
}


.recipe-item h3 {
    margin: 0 0 10px;
    font-size: 20px;
    color: #333;
}

.recipe-item p {
    margin: 0;
    color: #666;
}

.recipe-item:hover {
    transform: scale(1.05);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}
