.dietcalendar{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 100px;
    background-color: #f8f9fa;
    width: 1300px;
    height: 1200px;
}
.dietcalendar-add{
    display: flex;
    align-items: center;
    justify-content: center;
    background-color:#ffcc6d;
    width: 200px;
    height: 50px;
    border-radius: 50px;
    box-shadow: none;
    margin-left: 900px;
    border: none;
}
