.dietcalendar-button{
    width: 30px;
    height: 30px;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1000;
}
.modal-content {
    position: relative;
    width: 300px;
    height: 200px;
    background-color: white;
    padding: 20px;
    border-radius: 10px;
}
.model-close{
    position: absolute;
    top: 10px !important;
    right: 10px !important;
    cursor: pointer;
    border: none;
    background:none;
    font-size: 20px;
    width: 30px;
    height: 30px;
}
.modal-text{
    margin-top: 25px;
}